<template>
    <div class="view_section">
        <button class="view_entries" @click.prevent="getEntries">View Entries</button>
        <ul class="entries_list">
            <li v-for="(e, idx) of entries" :key="idx">
                {{ e }}
            </li>
        </ul>
    </div>
</template>

<script>
export default {
    name: 'GetEntriesForm',
    data() {
        return {
            entries: []
        }
    },

    methods: {
        async getEntries() {
            const myrole = localStorage.getItem('Health_care_role');
            const mycc = localStorage.getItem('Health_care_cc_rut');
            const mysession = localStorage.getItem('Health_care_session_key');

            const url = `/getEntries?myrole=${myrole}` + 
            `&mycc=${mycc}&mysession=${mysession}`;

            const resp = await fetch(url, {
                method: 'GET',
                headers: {
                    'Accept': 'application/json',
                    'Content-Type': 'application/json'
                }
            });

            const status = resp.status;
            const json = await resp.json();

            if (status != 200) {
                alert(json.msg);
                return;
            }

            console.log(json.entries);
            this.entries = json.entries;
        }
    }
}
</script>

