<template>
    <div class="body">
        <GetEntriesForm />
        <div class="form_section">
            <form class="new_entry" @submit.prevent="submit">
                <h3>New Entry Form</h3>
                <label for="patient_name">Patient Name:</label>
                <br>
                <input type="text" name="patient_name" v-model="patient_name">
                <br><br>

                <label for="patient_id">Patient Id:</label>
                <br>
                <input type="text" name="patient_id" v-model="patient_id">
                <br><br>

                <label for="observations">Observations:</label>
                <br>
                <textarea name="observations" cols="30" rows="10" v-model="observations"></textarea>
                <br><br>

                <label for="health_status">Healt status:</label>
                <br>
                <textarea name="health_status" cols="30" rows="10" v-model="health_status"></textarea>
                <br><br>
                <span>Your speciality will be writed in the report.</span>
                <br>
                <input type="submit" value="Create">
            </form>
        </div>
    </div>
</template>

<script>
import GetEntriesForm from './GetEntriesForm.vue';

export default {
    name: 'DoctorView',
    components: {
        GetEntriesForm
    },
    data() {
        return {
            entries: [],
            patient_id: '123456789',
            patient_name: 'my username',
            observations: 'this are some observations',
            health_status: 'this is a health status. Can be positive or negative or may have details.'
        }
    },
    methods: {
        async submit() {
            const form = {
                patient_id: this.patient_id,
                patient_name: this.patient_name,
                observations: this.observations,
                health_status: this.health_status,
                mycc: localStorage.getItem('Health_care_cc_rut'),
                myname: localStorage.getItem('Health_care_username'),
                mysession: localStorage.getItem('Health_care_session_key'),
                myrole: localStorage.getItem('Health_care_role')
            }
                

            const resp = await fetch("/newEntry", {
                method: 'POST',
                headers: {
                    'Accept': 'application/json',
                    'Content-Type': 'application/json'
                },
                body: JSON.stringify(form)
            });

            const json = await resp.json();

            alert(json.msg);
        }
    }
}
</script>

<style scoped>
.masterContainer {
    width: 100vw;
    height: 100vh;
}

.body {
    display: flex;

    justify-content: space-around;
}
</style>