
<template>
    <div class="masterContainer">
        <Navbar />
        <PatientHome v-if="home_role == 'patient'" />
        <DoctorHome v-if="home_role == 'doctor'" />
        <HospitalHome v-if="home_role == 'hospital'" />
    </div>
</template>

<script>
import PatientHome from '@/components/PatientHome.vue';
import DoctorHome from '@/components/DoctorHome.vue';
import HospitalHome from '@/components/HospitalHome.vue';
import Navbar from '@/components/Navbar.vue';

export default {
    name: 'HomeView',
    components: {
        PatientHome,
        DoctorHome,
        HospitalHome,
        Navbar
    },
    data() {
        return {
            home_role: ''
        }
    },
    mounted() {
        this.home_role = localStorage.getItem('Health_care_role');
        console.log(this.home_role);
    }
}
</script>

