<template>
    <div class="masterContainer">
        <GetEntriesForm />
        <div class="new_doctor">
            <form @submit.prevent="submit">
                <label for="username">Username: </label>
                <input type="text" name="username" v-model="username" required>
                <br>
                <label for="cc_nit">DNI or RUT: </label>
                <input type="text" name="cc_rut" v-model="cc_rut" required>
                <br>
                <label for="email">Email: </label>
                <input type="text" name="email" v-model="email" required>
                <br>
                <label for="tel">Tel: </label>
                <input type="text" name="tel" v-model="tel" required>
                <br>
                <label for="address">Address: </label>
                <input type="text" name="address" v-model="address" required>
                <hr>
                <br>

                <div class="doctor_extra">
                    <span>Specialization: </span>
                    
                    <input type="radio" name="services" value="internist" v-model="services" checked>
                    <label for="internist">Internist </label>
                    
                    <input type="radio" name="services" value="cardiologist" v-model="services">
                    <label for="cardiologist">Cardiologist </label>
                    
                    <input type="radio" name="services" value="optometra" v-model="services">
                    <label for="optometra">Optometra </label>
                    
                    <input type="radio" name="services" value="pediatrician" v-model="services">
                    <label for="pediatrician">Pediatrician </label>
                </div>
                <br>
                <input type="submit" value="Send!">
            </form>
            <br>
            <span v-show="password != ''">Default password: <strong>{{ password }}</strong></span>
        </div>
    </div>
</template>

<script>
import GetEntriesForm from './GetEntriesForm.vue';

export default {
    name: 'HospitalHome',
    components: {
        GetEntriesForm
    },
    data() {
        return {
            username: 'my doctor',
            cc_rut: '12365478932',
            email: 'some@example.com',
            tel: '3114567890',
            address: 'doctor address',
            services: 'internist',
            password: ''
        }
    },

    methods: {
        async submit() {
            const form = {
                username: this.username,
                cc: this.cc_rut,
                email: this.email,
                tel: this.tel,
                address: this.address,
                speciality: this.services,
                myname: localStorage.getItem('Health_care_username'),
                mycc: localStorage.getItem('Health_care_cc_rut'),
                myrole: localStorage.getItem('Health_care_role'),
                mysession: localStorage.getItem('Health_care_session_key')
            };

            const resp = await fetch("/newDoctor", {
                method: 'POST',
                headers: {
                    'Accept': 'application/json',
                    'Content-Type': 'application/json'
                },
                body: JSON.stringify(form)
            });

            const status = resp.status;
            const json = await resp.json();

            if (status != 200) {
                alert(json.msg);
                return;
            }

            this.password = json.password;
        }
    }
}
</script>

<style scoped>
.masterContainer {
    width: 100vw;
    height: 100vh;

    display: flex;
    justify-content: space-around;
}

</style>