<template>
    <div class="masterContainer">
        <form @submit.prevent="submit">
            <h2>Registration form</h2>
            <label for="username">Username: </label>
            <input type="text" name="username" v-model="username" required>
            <br>
            <label for="password">Password: </label>
            <input type="password" name="password" v-model="password" required>
            <br>
            <label for="confirm">Confirm: </label>
            <input type="password" name="confirm" v-model="confirm" required>
            <br>
            <div class="role">
                <span>Role: </span>
                <input type="radio" name="role" value="patient" v-model="role" checked>
                <label for="patient">Patient </label>

                <input type="radio" name="role" value="hospital" v-model="role">
                <label for="hospital">Hospital </label>
            </div>
            <label for="cc_nit">DNI or RUT: </label>
            <input type="text" name="cc_rut" v-model="cc_rut" required>
            <br>
            <label for="email">Email: </label>
            <input type="text" name="email" v-model="email" required>
            <br>
            <label for="tel">Tel: </label>
            <input type="text" name="tel" v-model="tel" required>
            <br>
            <label for="address">Address: </label>
            <input type="text" name="address" v-model="address" required>
            <hr>
            <br>

            <div class="patient_extra" v-if="role == 'patient'">
                <label for="date">Birthdate: </label>
                <input type="date" name="date" required>
            </div>

            <div class="hospital_extra" v-else>
                <span>Services: </span>
                
                <input type="checkbox" name="services" value="internist">
                <label for="internist">Internist </label>
                
                <input type="checkbox" name="services" value="cardiologist">
                <label for="cardiologist">Cardiologist </label>
                
                <input type="checkbox" name="services" value="optometra">
                <label for="optometra">Optometra </label>
                
                <input type="checkbox" name="services" value="pediatrician">
                <label for="pediatrician">Pediatrician </label>
            </div>
            <br>
            <input type="submit" value="Send!">
        </form>
    </div>
</template>

<script>
export default {
    name: 'RegisterView',
    data() {
        return {
            username: 'my username',
            password: 'my password',
            confirm: 'my password',
            role: 'patient',
            cc_rut: '123456789',
            email: 'some@example.co',
            tel: '3111234567',
            address: 'some address',
        }
    },

    methods: {
        async submit() {
            const username = this.username;
            const password = this.password;
            const confirm = this.confirm;
            const role = this.role;
            const cc = this.cc_rut;
            const email = this.email;
            const tel = this.tel;
            const address = this.address;

            if (password != confirm) {
                alert("Passwords don't match...");
                return;
            }

            const resp = await fetch("/register", {
                method: 'POST',
                headers: {
                    'Accept': 'application/json',
                    'Content-Type': 'application/json'
                },
                body: JSON.stringify({
                    username,
                    password,
                    role,
                    cc,
                    email,
                    tel,
                    address
                })
            });

            const status = resp.status;
            const json = await resp.json();

            if (status != 200) {
                alert(json.msg);
                return;
            }

            //Redirect in vue.
            localStorage.setItem('Health_care_role', role);
            localStorage.setItem('Health_care_session_key', json.session);
            localStorage.setItem('Health_care_username', username);
            localStorage.setItem('Health_care_cc_rut', cc);
            this.$router.push({path: '/'});
        }
    }
}
</script>

<style scoped>
.masterContainer {
    width: 100vw;
    height: 100vh;

    display: flex;
    justify-content: center;
    align-items: center;
}
</style>