<template>
    <nav class="navbar">
        <router-link class="to_logout" to="/login" @click="logout">Logout</router-link>
        <router-link class="to_register" to="/register" @click="logout">Register</router-link>
    </nav>
</template>

<script>
export default {
    name: 'Navbar',
    methods: {
        async logout() {
            const form = {
                mycc: localStorage.getItem('Health_care_cc_rut'),
                mysession: localStorage.getItem('Health_care_session_key'),
                myrole: localStorage.getItem('Health_care_role')
            }

            const resp = await fetch("/logout", {
                method: 'POST',
                headers: {
                    'Accept': 'application/json',
                    'Content-Type': 'application/json'
                },
                body: JSON.stringify(form)
            });

            const status = resp.status;

            if (status != 200) {
                const json = await resp.json();
                alert(json.msg);
                return;
            }

            localStorage.removeItem('Health_care_cc_rut');
            localStorage.removeItem('Health_care_session_key');
            localStorage.removeItem('Health_care_role');
            localStorage.removeItem('Health_care_username');
        }
    }
}
</script>

<style scoped>
.navbar {
    width: 100%;
    height: 50px;
    background-color: rgb(172, 172, 172);
}

.navbar > a {
    color: rgb(153, 0, 0);
}

.to_register {
    margin-left: 50px;
}
</style>