<template>
    <div class="masterContainer">
        <form @submit.prevent="submit">
            <label for="cc_nit">DNI or RUT: </label>
            <input type="text" name="cc_rut" v-model="cc_rut" required>
            <br>
            <label for="password">Password: </label>
            <input type="password" name="password" v-model="password" required>
            <br>
            <input type="submit" value="Send!">
        </form>
    </div>
</template>

<script>
export default {
    name: 'LoginView',
    data() {
        return {
            cc_rut: '12365478932',
            password: 'g1313AiJ6N'
        }
    },
    methods: {
        async submit() {
            const cc = this.cc_rut;
            const password = this.password;

            const resp = await fetch("/login", {
                method: 'POST',
                headers: {
                    'Accept': 'application/json',
                    'Content-Type': 'application/json'
                },
                body: JSON.stringify({
                    cc,
                    password
                })
            });

            const status = resp.status;
            const json = await resp.json();

            if (status != 200) {
                alert(json.msg);
                return;
            }

            //Redirect in vue.
            localStorage.setItem('Health_care_role', json.role);
            localStorage.setItem('Health_care_session_key', json.session);
            localStorage.setItem('Health_care_username', json.username);
            localStorage.setItem('Health_care_cc_rut', cc);
            this.$router.push({path: '/'});
        }
    }
}
</script>

